:root {
  --background: #0e1015;
  --primary: #f11036;
  --secondary: #a0112d;
  --third: #181a20;
}

body {
  background-color: var(--background);
}

.btn {
  display: flex;
  width: 8em;
  height: 2.3em;
  background-color: var(--primary);
  color: white;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
}

.btn:hover {
  background-color: var(--secondary);
}

.btnServer {
  cursor: pointer;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--primary);
  width: 3.5em;
  height: 1.8em;
}
.btnServer.activo {
  cursor: pointer;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--secondary);
  width: 3.5em;
  height: 1.8em;
}

.btnSearch {
  border: none;
  background-color: var(--primary);
  width: 4em;
  height: 2.5em;
  border-radius: 2px;
  color: white;
  font-weight: bold;
  cursor: pointer;
  grid-column: 2/3;
  justify-self: flex-end;
}

.btnSearch:hover {
  background-color: var(--secondary);
}

.btnSesion {
}

.flexUl {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style-type: none;
}

.loader {
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  text-align: center;
}

.grid {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  padding: 0 10%;
}

.donation {
  display: flex;
  justify-content: center;
}
.donate-ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 2em;
  font-weight: bold;
}

.donate-ul li {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--primary);
  height: 10vh;
  width: 30%;
  margin: 0.3em 0;
}

@media screen and (max-width: 800px) {
  .donate-ul li {
    width: 80%;
  }
}
