.resultados-section {
  display: flex;
  flex-direction: column;
  color: white;
  justify-content: center;
  align-items: center;
}

.resultados-nombres {
  display: flex;
  margin-top: 1.5em;
}
.resultados-nombres li {
  margin: 1em 1em;
}
.resultados-stats {
  display: flex;
}
.resultados-stats li {
  margin: 1em 2em;
}
.section-actions {
  display: flex;

  margin-bottom: 5em;
  align-items: center;
  justify-content: center;
}

.section-actions a {
  color: white;
  text-decoration: none;
}

.section-actions ul {
  display: flex;
}

.section-actions li {
  display: flex;
  background-color: var(--primary);
  margin: 1em 1em;
  width: 8em;
  height: 2.3em;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
}
