nav {
  height: 10vh;
}

.title {
  display: flex;
  flex-direction: column;
  height: 50vh;
  align-items: center;
  justify-content: center;
}

h1 {
  grid-column: 3/7;
  font-size: 7em;
  color: var(--primary);
}
h2 {
  color: white;
  grid-column: 3/7;
}

#Start main {
  height: 30vh;
}

#Start-ul a {
  text-decoration: none;
}

#start-ul li {
  margin: 0 20px;
}

#start-ul {
  grid-column: 3/7;
  justify-self: center;
}

footer {
  display: flex;
  height: 10vh;
  align-items: center;
  justify-content: center;
}
