.usersd {
  display: flex;
  flex-direction: column;
  justify-content: center;
  list-style-type: none;
  background-color: var(--third);
  width: 12%;
  height: 10em;
  border-radius: 5px;
  color: white;
  border: 2px solid white;
}

.usersd-name {
  text-decoration: underline;
  margin-bottom: 1em;
}

@media screen and (max-width: 800px) {
  .usersd {
    width: 50%;
    margin: 10px 0;
  }
}
