#Play {
  display: grid;
  grid-template-rows: 3em 30em 4em;
}

.choose-sesion {
  align-self: center;
}

.choose-sesion ul {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  margin-top: 3em;
  align-items: center;
}

.choose-sesion li {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--primary);
  width: 8em;
  height: 2.3em;
  margin: 10px 0;
  color: white;
  border-radius: 5px;
}

.session {
  align-self: center;
}

ul {
  list-style-type: none;
}

.ul-interactive {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}
.ul-interactive li {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 1em;
  background-color: var(--primary);
  width: 7.5em;
  height: 3em;
  font-size: 1.2em;
  font-weight: bold;
}

.ul-names {
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}
.ul-names li {
  margin: 0 1em;
  font-size: 1.2em;
}

.ul-results {
  margin-top: 1em;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ul-results li {
  font-size: 1.1em;
  color: var(--primary);
  margin: 0 2.5em;
}
