input[placeholder] {
  color: var(--primary);
  font-weight: bold;
  text-align: center;
}

h4 {
  grid-column: 3/7;
  font-size: 7em;
  color: var(--primary);
}

h5 {
  grid-column: 3/7;
  font-size: 1.3em;
  color: white;
}

#Dodge section {
  height: 12vh;
}

form {
  align-self: center;
}

.server-selection {
  margin-top: 0.4em;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.server-selection li {
  margin: 0 10px;
}

.buscador {
  outline: none;
  grid-column: 3/7;
  border: none;
  height: 2.5em;
  border-radius: 2px;
  border: 1px solid var(--primary);
  background-color: var(--third);
}

.dodge-main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: center;
  height: 40vh;
}

.usersd-section {
  display: flex;
  justify-content: space-evenly;
  margin-top: 2em;
}

.advice {
  display: flex;
  align-self: center;
  align-items: center;
  justify-content: center;
  background-color: var(--primary);
  width: 80%;
  height: 10vh;
  font-size: 2em;
  margin-top: 3em;
  color: white;
}

.dodge-results {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@media screen and (max-width: 800px) {
  #Dodge {
    display: grid;
    grid-template-rows: 3em 1fr 1fr auto 8em;
  }

  h4 {
    font-size: 5em;
  }
  h5 {
    font-size: 1em;
  }
  .buscador {
    width: 17em;
  }
  .usersd-section {
    flex-direction: column;
    align-items: center;
  }
}
