.userCard {
  margin-top: 2em;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 4em;
}
.userCard-ul {
  display: flex;
  margin-top: 2em;
}

.userCard-ul li {
  margin: 0 1em;
  color: var(--primary);
  text-decoration: underline;
}
#UserCard {
  background-color: var(--primary);
  width: 40%;
  border-radius: 5px;
  color: white;
}

.section-info {
  margin-top: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.section-info li {
  margin: 0 10px;
}

.section-liga {
  display: flex;
  align-items: center;
  justify-content: center;
}

.section-liga-position {
  display: flex;
  align-items: center;
  justify-content: center;
}

.section-liga-position img {
  width: 15%;
}

.section-liga-li {
  display: flex;
  align-items: center;
  justify-content: center;
}

.section-liga-stats {
  display: flex;
  align-items: center;
  justify-content: center;
}

.section-stats {
  margin-bottom: 1em;
  display: flex;
  align-items: center;
  justify-content: center;
}

.section-stats ul {
  margin: 0 15px;
}

.section-winsloss {
  display: flex;
  align-items: center;
  justify-content: center;
}
.section-winsloss li {
  margin: 0 0.5em;
}

.section-totalwinrate {
  display: flex;
  align-items: center;
  justify-content: center;
}

.section-totalwinrate li {
  margin: 0 0.5em;
}

.tables {
  margin: 1em 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.fail {
  color: #ff4356;
  margin: 5em 0;
}

#Analysis main {
  margin: 1em 0;
}
@media screen and (max-width: 800px) {
  #UserCard {
    background-color: #ff4356;
    width: 90%;
    border-radius: 5px;
    color: white;
  }
}
